@import 'styles/constants';
@import 'tools/styles/flex';
@import 'tools/styles/adaptive';
@import 'styles/fonts';

.home {
    position: relative;

    width: 100%;
    min-height: 100vh;
    overflow: hidden;

    background: $black;
    background-image: url('./media/background.png');
    background-position: top center;
    background-repeat: no-repeat;

    .mediumLogo {
        @include dFlex();
        @include flexRow();
        @include justifyContentCenter();

        margin-bottom: 52px;

        img {
            width: 91px;
            height: 101px;
        }
    }

    .smallLogo {
        display: none;
        @include flexRow();
        @include justifyContentCenter();

        margin-bottom: 16px;

        img {
            width: 49px;
            height: 55px;
        }
    }

    .main {
        @include dFlex();
        @include flexRow();
        @include justifyContentCenter();
        @include alignItemsCenter();

        .logo {
            display: none;

            margin-right: 10px;
            z-index: 1;

            img {
                width: 415px;
                height: 464px;
            }
        }

        .title {
            @include fontRobotoCondensed700();

            color: $white;
            text-transform: uppercase;

            padding: 0 25px;

            z-index: 2;
        }
    }

    .containerDescription {
        @include dFlex();
        @include flexRow();
        @include justifyContentCenter();

        width: 100%;

        .description {
            font-weight: 400;
            color: $white;

            .red {
                @include fontRaleway800();
                color: $red;

                margin-right: 5px;
            }
        }
    }

    .cards {
        @include dFlex();
        padding: 0 25px;

        .card {
            width: 100%;
            overflow: hidden;
        }

        .cardCenter {
            width: 100%;
            overflow: hidden;
        }
    }

    .buttons {
        @include dFlex();
        @include flexColumn();
        @include alignItemsCenter();

        margin-top: 70px;
    }
}

@include applyResponsive320 {
    .home {
        padding-top: 71px;
        padding-bottom: 56px;

        .main {
            margin-bottom: 12px;

            .title {
                @include dFlex();
                @include justifyContentStart();

                width: 100%;

                font-size: 32px;
                line-height: 39px;
            }
        }

        .containerDescription {
            width: 66%;
            min-width: 300px;

            .description {
                width: 300px;

                font-size: 16px;
                line-height: 18px;

                padding-left: 25px;
                margin-bottom: 62px;
            }
        }

        .cards {
            @include flexColumn();
            @include alignItemsCenter();

            .card {
                width: 100%;
                max-width: 460px;
                height: 179px;
            }

            .cardCenter {
                width: 100%;
                max-width: 460px;
                height: 166px;
            }
        }

        .buttons {
            .button {
                width: 213px;
                height: 52px;
            }
        }
    }
}

@include applyResponsive428 {
    .home {
        .main {
            .title {
                font-size: 44px;
                line-height: 51px;
            }
        }
    }
}

@include applyResponsive512 {
    .home {
        .main {
            .title {
                @include justifyContentCenter();
                margin-right: 80px;
            }
        }
    }
}

@include applyResponsive600 {
    .home {
        .main {
            .title {
                font-size: 51px;
                line-height: 57px;
            }
        }

        .containerDescription {
            width: 72%;
            min-width: 350px;

            .description {
                width: 350px;

                line-height: 19px;
                font-size: 18px;
            }
        }

        .buttons {
            .button {
                width: 266px;
                height: 60px;
            }
        }
    }
}

@include applyResponsive768 {
    .home {
        .main {
            .title {
                @include justifyContentCenter();

                font-size: 65px;
                line-height: 73px;

            }
        }

        .containerDescription {
            min-width: 400px;

            .description {
                width: 400px;

                font-size: 20px;
                line-height: 25px;
            }
        }
    }
}

@include applyResponsive1024 {
    .home {
        padding-top: 48px;
        padding-bottom: 48px;

        .mediumLogo {
            display: none !important;
        }

        .smallLogo {
            @include dFlex();
        }

        .main {
            margin-bottom: 70px;

            .logo {
                display: block;
            }

            .title {
                width: auto;

                font-size: 50px;
                line-height: 62px;
            }
        }

        .containerDescription {
            width: auto;

            .description {
                width: 100%;

                font-size: 24px;
                text-align: center;

                padding: 0 70px;
            }
        }

        .cards {
            @include flexRow();
            @include justifyContentCenter();

            .card {
                width: 458px;
                height: 200px;
            }

            .cardCenter {
                width: 494px;
                height: 200px;
            }
        }

        .buttons {
            .button {
                width: 310px;
                height: 70px;
            }
        }
    }
}

@include applyResponsive1152 {
    .home {
        .main {
            .title {
                font-size: 60px;
                line-height: 78px;
            }
        }
    }
}

@include applyResponsive1280 {
    .home {
        .main {
            .title {
                font-size: 80px;
                line-height: 94px;
            }
        }

        .cards {
            .card {
                height: 218px;
            }

            .cardCenter {
                height: 218px;
            }
        }
    }
}

@include applyResponsive1680 {
    .home {
        padding-top: 48px;
        padding-bottom: 48px;

        .main {
            margin-bottom: 70px;

            .logo {
                margin-right: 105px;
            }

            .title {
                width: auto;

                font-size: 101px;
                line-height: 119px;
            }
        }
    }
}