@import 'styles/constants';
@import 'tools/styles/flex';
@import 'tools/styles/adaptive';
@import 'styles/fonts';

.card {
    @include dFlex();
    @include flexRow();
    @include justifyContentCenter();
    @include alignItemsCenter();

    position: relative;

    border: 1px solid $borderBlue;
    background: $black;

    z-index: 2;

    .image {
        position: absolute;

        top: 0;
        right: 0;
        left: 0;
        bottom: 0;

        z-index: 0;
    }

    .title {
        @include fontRobotoCondensed700();

        color: $white;
        text-align: center;
        text-transform: uppercase;

        z-index: 40 !important;
    }
}

@include applyResponsive320 {
    .card {
        .title {
            font-size: 27px;
            line-height: 31px;
        }
    }
}

@include applyResponsive428 {
    .card {
        .title {
            font-size: 34px;
            line-height: 40px;
        }
    }
}

@include applyResponsive1024 {
    .card {
        .title {
            font-size: 31px;
            line-height: 31px;
        }
    }
}

@include applyResponsive1152 {
    .card {
        .title {
            font-size: 36px;
            line-height: 42px;
        }
    }
}